@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

body {
  font-family: Inter, Tahoma, Geneva, Verdana, sans-serif;
  background: #f0f0f0;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.registration-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #fff;
  padding: 40px;
  border-radius: 4vw;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  width: 40vw;
  height: 60vh;
  min-height: 450px;
  min-width: 300px;
  max-width: 480px;
}

.header-register {
  display: flex;
  align-items: center;
  flex-basis: 80px;
}

.back-arrow {
  width: 30px;
  cursor: pointer;
  font-size: 20px;
}

.registration-form h2 {
  flex-grow: 1;
  text-align: center;
  font-size: 2rem;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 400px;
}



form > div {
  display: flex;
  flex-direction: column;
  font-size: 0.85rem;
  font-weight: bold;
  text-indent: 2vw;
}
.message {
  display: block;
  margin-top: -10px;
  margin-bottom: 10px;
  color: #808080;
  font-size: 45%;
  text-indent: 8%;
}
.error-message {
  color: red;
}

.success-message {
  color: blue;
}


form input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 1.3vh;
  height: 2.2vh;
  outline: none;
  transition: 0.1s;
  
  font-weight: 600;
  color:#444;
}
form input::placeholder {
  color: #aaa;
}
form input:active {
  background-color: #fbfbfb;
}
form input:focus {
  box-shadow: inset 0 0 5px -3px #555;
}

.registration-form button {
  border: none;
  border-radius: 1.3vh;
  height: 40px;
  background-color: #E1DAC8;
  color: white;
  cursor: pointer;
  transition: 0.1s;
}
.registration-form button:hover {
  box-shadow: inset 0 0 5px 0px #dccdb2;
}
.registration-form button:active {
  background-color: #DBD4C1;
}

.registration-form small {
  display: block;
  margin-top: -10px;
  margin-bottom: 10px;
  color: #808080;
  font-size: 45%;
  text-indent: 8%;
}

